<template>
    <div>
        <b-list-group-item href="#" v-b-toggle="'collapse-2' + order.id" @click="getDetail"> 언제? {{ order.orderTime | prettyTime }}, 어디서?{{ order.orderTable }}</b-list-group-item>

        <b-collapse :id="'collapse-2' + order.id">
            <b-card>
                <b-alert show variant="success">
                    <h4 class="alert-heading">주문 상세</h4>
                    <b-container>
                        <b-row>
                            <b-col cols="6" v-for="(detail, idx) of details" :key="idx">
                                <b-list-group-item class="d-flex align-items-center">
                                    <b-avatar class="mr-3" :src="img_url + '/menu/' + detail.img"></b-avatar>
                                    <span class="mr-auto">품명: {{ detail.name }}, 단가: {{ detail.unitprice }}원, {{ detail.quantity + "잔" }}</span>
                                    <b-badge>{{ detail.unitprice * detail.quantity }}원</b-badge>
                                </b-list-group-item>
                            </b-col>
                        </b-row>
                    </b-container>
                    <hr />
                    <p class="mb-0">총 비용:{{ totalPrice }}, 스탬프 적립: {{ stamp }}</p>
                </b-alert>
            </b-card>
        </b-collapse>
    </div>
</template>
<style scoped></style>
<script>
import consts from "@/consts";
export default {
    props: {
        order: Object,
    },
    data() {
        return {
            img_url: consts.img_url,
            selected: false,
            stamp: 0,
            details: [],
            totalPrice: 0,
        };
    },
    filters: {
        prettyTime(datetime) {
            let date = datetime.substring(0, 10);
            let time = datetime.substring(11, 19);

            return date + " " + time;
        },
    },
    methods: {
        getDetail() {
            if (!this.selected) {
                consts
                    .axios({
                        method: "get",
                        url: "/order/" + this.order.id,
                    })
                    .then((res) => {
                        this.selected = true;
                        this.details = res.data;
                        for (let data of this.details) {
                            this.totalPrice += data.totalprice;
                            this.stamp = data.stamp;
                        }

                        // console.log(res);
                    });
            }
        },
    },
};
</script>
