<template>
    <div>
        <h1>회원 가입</h1>
        <b-form v-if="show">
            <b-form-group id="input-group-1" :label="'아이디:'" label-for="input-1">
                <span :class="variant">{{ dupmsg }}</span>
                <b-form-input id="input-1" v-model="form.id" type="text" required @blur="checkId"></b-form-input>
            </b-form-group>

            <b-form-group id="input-group-2" label="이름:" label-for="input-2"> <b-form-input id="input-2" v-model="form.name" required></b-form-input><br /> </b-form-group>

            <b-form-group id="input-group-3" label="비밀번호:" label-for="input-3">
                <b-form-input type="password" id="input-3" v-model="form.pass" required></b-form-input>
            </b-form-group>
        </b-form>
        <div class="right">
            <b-button-group>
                <b-button variant="success" :disabled="idDup" @click="join">가입</b-button>
                <b-button variant="info">취소</b-button>
            </b-button-group>
        </div>
    </div>
</template>
<style scoped>
* {
    text-align: start;
}

.right {
    text-align: end;
}
.error {
    color: red;
}
.success {
    color: green;
}
</style>
<script>
import consts from "@/consts";
export default {
    data() {
        return {
            dupmsg: "",
            idDup: true,
            form: {
                id: "",
                name: "",
                pass: "",
            },
            variant: "",
            show: true,
        };
    },
    methods: {
        checkId() {
            if (!this.form.id) {
                return;
            }
            consts
                .axios({
                    method: "get",
                    url: "/user/isUsed",
                    params: {
                        id: this.form.id,
                    },
                })
                .then((res) => {
                    // console.log(res);
                    if (!res.data) {
                        this.dupmsg = "사용 가능한 아이디입니다.";
                        this.idDup = false;
                        this.variant = "success";
                    } else {
                        this.dupmsg = "다른 아이디를 사용해주세요.";
                        this.idDup = true;
                        this.variant = "error";
                    }
                });
        },
        join() {
            if (!this.form.id) {
                alert("아이디를 입력하세요.");
                return;
            }
            if (!this.form.name) {
                alert("이름을 입력하세요.");
                return;
            }
            if (!this.form.pass) {
                alert("비밀번호를 입력하세요.");
                return;
            }
            consts
                .axios({
                    method: "post",
                    url: "/user",
                    data: this.form,
                })
                .then((res) => {
                    console.log(res);
                    if (res.data) {
                        alert("가입 되었습니다. 로그인 후 다시 이용해주세요.");
                        this.$router.push({
                            path: "/",
                        });
                    }
                });
        },
        onReset(event) {
            event.preventDefault();
            // Reset our form values
            this.form.email = "";
            this.form.name = "";
            this.form.food = null;
            this.form.checked = [];
            // Trick to reset/clear native browser form validation state
            this.show = false;
            this.$nextTick(() => {
                this.show = true;
            });
        },
    },
};
</script>
