<template>
    <div class="start">
        <b-alert show variant="success">
            <h4 class="alert-heading">우리 {{ user.name }}회원님은요~~</h4>
            <hr />
            <p class="mb-0">
                로그인 할 때 아이디는 {{ user.id }}을 사용합니다.<br />
                현재 모은 스탬프는 총 <b-badge variant="light">{{ user.stamps }}</b-badge
                >개로
                <b-img v-if="grade.img != ''" class="grade" :src="img_url + '/grade/' + grade.img" alt="Fluid-grow image"></b-img>
                {{ grade.title }}-{{ grade.step }} 단계입니다. <br />
                앞으로 <b-badge variant="light">{{ grade.to }}</b-badge
                >만 더 모으면 다음 단계입니다<b-icon icon="graph-up" aria-hidden="true"></b-icon>!!<br />
            </p>
        </b-alert>
        <hr />
        <b-card-group deck>
            <b-card header="이제까지 주문 내역은" variant="success">
                <p class="card-text mt-2">
                    주문 정보를 클릭하면 주문 내역을 살펴볼 수 있습니다.
                </p>
                <b-list-group v-for="(order, idx) in orders" :key="idx">
                    <order-info :order="order"></order-info>
                </b-list-group>
            </b-card>
        </b-card-group>
    </div>
</template>
<style scoped>
.start {
    text-align: start;
}
.grade {
    width: 1em;
    height: 1em;
}
</style>
<script>
import consts from "@/consts";
import OrderInfo from "./OrderInfo.vue";
export default {
    data() {
        return {
            img_url: consts.img_url,
            user: { id: "", name: "" },
            orders: [],
            grade: { title: "", step: 1, to: 0, img: "" },
        };
    },
    props : {
        loginId : {type:String},
    },
    created() {
        let pass = prompt("정보확인을 위해 비밀번호를 입력해주세요.");
        if (!pass) {
            this.$router.push({
                path: "/",
            });
        } else {
            consts
                .axios({
                    method: "post",
                    url: "/user/info",
                    data: { id: this.loginId, pass: pass },
                })
                .then((res) => {
                    if (!res.data) {
                        alert("비밀 번호를 확인하세요.");
                        this.$router.push({
                            path: "/",
                        });
                    } else {
                        this.user = res.data.user;
                        this.orders = res.data.order;
                        // console.log("응답 결과", res.data);
                        this.grade = res.data.grade;
                    }
                });
        }
    },
    components: {
        OrderInfo,
    },

    methods: {
        checkGrade() {},
    },
};
</script>
