<template>
    <div>
        <b-alert show variant="success">
            <h4 class="alert-heading">상품 평점</h4>
            <b-container class="bv-example-row">
                <b-row>
                    <b-col cols="4"><b-img :src="img" fluid alt="Responsive image"></b-img></b-col>
                    <b-col cols="8"
                        ><b-list-group>
                            <b-list-group-item>상품명: {{ name }}</b-list-group-item>
                            <b-list-group-item>상품단가: {{ price }}</b-list-group-item>
                            <b-list-group-item>총주문수량:{{ sells }}</b-list-group-item>
                            <b-list-group-item>평가회수:{{ commentCnt }}</b-list-group-item>
                            <b-list-group-item>평균평점: {{ avg }}</b-list-group-item>
                        </b-list-group></b-col
                    >
                </b-row>
                <br />
                <b-row align-h="end">
                    <b-button @click="insertComment">한줄평 남기기</b-button>
                </b-row>
            </b-container>
            <hr />
            <p class="mb-0">
                <b-table-simple striped hover small caption-top responsive>
                    <caption>
                        자신이 남긴 평가만 수정, 삭제할 수 있습니다.
                    </caption>
                    <colgroup>
                        <col />
                        <col />
                        <col />
                        <col />
                    </colgroup>
                    <b-thead head-variant="dark">
                        <b-tr>
                            <b-th>사용자</b-th>
                            <b-th>평점</b-th>
                            <b-th>한줄평</b-th>
                            <b-th></b-th>
                        </b-tr>
                    </b-thead>
                    <b-tbody>
                        <b-tr v-for="(item, idx) in items" :key="idx">
                            <b-td>{{ item.userName }}</b-td>
                            <b-td>{{ item.rating }}</b-td>
                            <b-td>{{ item.comment }}</b-td>
                            <b-td
                                ><div v-if="item.user_id == loginId">
                                    <div class="mt-3">
                                        <b-button-group>
                                            <b-button variant="success" @click="updateComment(item)">수정</b-button>
                                            <b-button variant="info" @click="deleteComment(item.commentId, item.comment)">삭제</b-button>
                                        </b-button-group>
                                    </div>
                                </div></b-td
                            >
                        </b-tr>
                    </b-tbody>
                </b-table-simple>
            </p>
        </b-alert>
        <b-modal id="insert-modal" hide-footer title="한줄평 남기기" ref="insert-modal">
            <div>
                <b-form>
                    <b-form-group id="input-group-1" label="평점: " label-for="input-1">
                        <b-form-input id="input-1" v-model="comment.rating" type="number" required max="10"></b-form-input>
                    </b-form-group>

                    <b-form-group id="input-group-2" label="한줄평:" label-for="input-2">
                        <b-form-input id="input-2" v-model="comment.comment" required></b-form-input>
                    </b-form-group>
                    <b-row align-h="end">
                        <b-col cols="3"><b-button @click="insert" variant="primary">등록</b-button></b-col>
                    </b-row>
                </b-form>
            </div>
        </b-modal>

        <b-modal id="update-modal" hide-footer title="한줄평 수정하기" ref="update-modal">
            <div>
                <b-form>
                    <b-form-group id="input-group-1" label="평점: " label-for="input-1">
                        <b-form-input id="input-1" v-model="comment.rating" type="number" required max="10"></b-form-input>
                    </b-form-group>

                    <b-form-group id="input-group-2" label="한줄평:" label-for="input-2">
                        <b-form-input id="input-2" v-model="comment.comment" required></b-form-input>
                    </b-form-group>
                    <b-row align-h="end">
                        <b-col cols="3"><b-button @click="update" variant="primary">수정</b-button></b-col>
                    </b-row>
                </b-form>
            </div>
        </b-modal>
    </div>
</template>
<style scoped>
col:nth-child(1) {
    width: 20%;
}
col:nth-child(2) {
    width: 10%;
}
col:nth-child(3) {
    width: 50%;
}
</style>

<script>
import consts from "@/consts";
export default {
    data() {
        return {
            fields: [{ key: "userName" }, { key: "rating" }, { key: "comment" }],
            img_url: consts.img_url,
            img: "",
            name: "",
            price: 0,
            sells: 0,
            commentCnt: 0,
            avg: 0,
            id: 0,
            items: [],
            comment: {
                id: 0,
                userId: "",
                productId: 0,
                rating: 0,
                comment: "",
            },
            loginId : "",
        };
    },
    created() {
        this.id = this.$route.params.id;
        this.reload();
        this.loginId = sessionStorage.getItem("loginId")
    },
    methods: {
        insert() {
            consts
                .axios({
                    method: "post",
                    url: "/comment",
                    data: this.comment,
                })
                .then((res) => {
                    if (res.data) {
                        this.reload();
                        this.$refs["insert-modal"].hide();
                    }
                });
        },
        insertComment() {
            this.comment = {
                userId: this.loginId,
                productId: this.id,
                rating: 0,
                comment: "",
            };
            this.$refs["insert-modal"].show();
        },
        deleteComment(id, comment) {
            let del = window.confirm(comment + "를 삭제하시겠습니까?");
            if (del) {
                consts
                    .axios({
                        method: "delete",
                        url: "/comment/" + id,
                    })
                    .then((res) => {
                        if (res.data) {
                            this.reload();
                        }
                    })
                    .catch();
            }
        },
        updateComment(comment) {
            // console.log(comment);
            this.comment.id = comment.commentId;
            this.comment.userId = comment.user_id;
            this.comment.productId = this.id;
            this.comment.rating = comment.rating;
            this.comment.comment = comment.comment;
            this.$refs["update-modal"].show();
        },
        update() {
            consts
                .axios({
                    method: "put",
                    url: "/comment",
                    data: this.comment,
                })
                .then((res) => {
                    if (res.data) {
                        this.$refs["update-modal"].hide();
                        this.reload();
                    }
                })
                .catch();
        },
        reload() {
            consts
                .axios({
                    method: "get",
                    url: "/product/" + this.id,
                })
                .then((res) => {
                    this.items = res.data;
                    if (res.data) {
                        for (let data of res.data) {
                            // console.log(data);
                            this.img = consts.img_url + "/menu/" + data.img;
                            this.price = data.price;
                            this.name = data.name;
                            this.sells = data.sells;
                            this.avg = data.avg;
                            this.commentCnt = data.commentCnt;
                        }
                    }
                });
        },
    },
};
</script>
