<template>
    <div id="floatdiv" v-if="Object.keys(order_menu).length" >
        <p>주문서 작성중</p>
        <p />
        <b-list-group style="max-width: 300px;">
            <b-list-group-item class="d-flex align-items-center" v-for="(item, key, idx) in order_menu" :key="idx">
                <template v-if="item">
                    <b-avatar variant="info" :src="item.img" class="mr-3"></b-avatar>
                    <span class="mr-auto">{{ item.name }}</span
                    >&nbsp;
                    <b-badge>{{ item.cnt }}</b-badge>
                </template>
            </b-list-group-item>
            <b-button variant="primary" @click="order">주문</b-button>
        </b-list-group>
    </div>
</template>
<style scoped>
#floatdiv {
    position: fixed;
    _position: absolute;
    _zindex: -1;
    overflow: hidden;
    right: 0px;
    top: 60px;
    background-color: transparent;
    margin: 0;
    padding: 0;
}
.hidden {
    display: none;
}
</style>
<script>
import consts from "@/consts";
import Vue from "vue";
import bus from "../utils/bus.js"

export default {
    data(){
        return {
            order_menu: {},
            order_completed : false,
            loginId :"",
        }
    },
    methods: {
        order() {
            let details = [];

            for (let key in this.order_menu) {
                let item = this.order_menu[key];
                details.push({ productId: key, quantity: item.cnt });
            }

            let myOrder = { userId: this.loginId, orderTable: "웹주문", orderTime: new Date(), completed: "N", details };
            consts
                .axios({
                    method: "post",
                    url: "/order",
                    data: myOrder,
                })
                .then((res) => {
                    if (res.data) {
                        alert("주문 되었습니다.");
                        this.clearAll();
                    }
                });
        },
        setMenu(payload){
            this.order_completed = false;
            if (payload.cnt == 0) {
                Vue.delete(this.order_menu, payload.id);
            } else {
                // this.order_menu[payload.id] = { cnt: payload.cnt, img: payload.img, name: payload.name };
                Vue.set(this.order_menu, payload.id, { cnt: payload.cnt, img: payload.img, name: payload.name });
            }
        },
        clearAll(){
            this.order_completed = true;
            this.order_menu = {};
            bus.$emit("clearOrder");
        }

    },
    created (){
        bus.$on("setMenu", (payload) => {
            this.setMenu(payload);
        }),

        bus.$on("logout", () => {
                this.clearAll()
        })

        bus.$on("login", (loginId) => {
            this.loginId = loginId
        })

        let id = sessionStorage.getItem("loginId");
        if(id != null && id != ""){ 
            this.loginId = id; 
        }
        
    },
    destroyed (){
        bus.$off("setMenu");
        bus.$off("logout");
        bus.$off("login");
    }
};
</script>
